.intro {
  @include padding(91px,72px);
  color: $basic_white;
  background-image: url("../img/intro-catalog-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: cover;
  background-color: $basic_color;
  overflow: hidden;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/intro-catalog-desktop-2x.jpg");
  }

  @media (max-width: $desktop-width) {
    background-image: url("../img/intro-catalog-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/intro-catalog-tablet-2x.jpg");
    }
  }

  @media (max-width: $tablet-width) {
    @include padding(79px,61px);
    background-image: url("../img/intro-catalog-mobile.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/intro-catalog-mobile-2x.jpg");
    }
  }
}

.intro__wrapper {
  text-align: center;

  & > h1 {
    @include text(34px, 44px, 700);
    text-transform: uppercase;
    margin-bottom: 3px;

    @media (max-width: $tablet-width) {
      @include text(34px, 44px, 700);
    }
  }

  & > p {
    @include text(22px, 29px, 500);
    font-family: $caption-font;
    font-style: italic;
    margin-top: 0;

    @media (max-width: $tablet-width) {
      @include text(22px, 26px, 500);
    }
  }
}
