.close-button {
  @include pseudo($display: block, $pos: absolute, $content: "");
  height: 20px;
  width: 20px;
  background-color: transparent;
  cursor: pointer;
  border: 0;

  &::before {
    @include pseudo($display: block, $pos: absolute, $content: "");
    width: 15px;
    height: 15px;
    top: 0;
    right: 0;
    border-top: 1px solid $basic_color;
    border-right: 1px solid $basic_color;
    transform: rotate(135deg);
  }
}

.acc_active .questions__button,
.acc_active .filter__accordion {
  &.close-button::before {
    transform: rotate(-45deg);
  }

  & ~ p {
    display: none;
  }
}

.nojs {
  display: none;
}
